import ScrollReveal from './ScrollReveal'

export default function Services() {
  const items = [
    { 
      title: 'Web Development', 
      body: 'Next.js, React, Node.js, API design',
      img: '/images/services/web.png'
    },
    { 
      title: 'UI/UX Design', 
      body: 'Design systems, prototyping, research',
      img: '/images/services/design.png'
    },
    { 
      title: 'Brand Strategy', 
      body: 'Identity, messaging, positioning',
      img: '/images/services/brand.png'
    },
  ]

  return (
    <ScrollReveal className="py-20">
      <div className="container mx-auto">
        <h2 className="text-3xl font-semibold text-center mb-8">Services</h2>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          {items.map(item => (
            <ScrollReveal key={item.title} delay={0.05}>
              <div className="p-6 bg-white/60 backdrop-blur rounded-lg shadow hover:shadow-lg transition">
                <img src={item.img} alt="" className="h-20 mx-auto mb-4 object-contain" />
                <h3 className="font-bold text-xl text-center">{item.title}</h3>
                <p className="mt-2 text-gray-700 text-center">{item.body}</p>
              </div>
            </ScrollReveal>
          ))}
        </div>
      </div>
    </ScrollReveal>
  )
}
